/* 首页布局 摘要 标签 */
#recent-posts > .recent-post-item > .recent-post-info {
    display: flex !important;
    flex-direction: column !important;
    padding: 0 32px !important;
    justify-content: center !important;
    gap: 6px;
}

#content-inner #recent-posts > .recent-post-item > .recent-post-info > .content {
    opacity: 1 !important;
    height: auto !important;
    line-height: 1.5 !important;
    margin-top: 0 !important;
    font-size: 15px !important;
}

.recent-post-info-top-tips {
    display: flex;
    gap: 10px;
    position: absolute;
    z-index: 999;
    background: rgb(255 255 255 / 20%);
    padding: 0 5px;
    margin: 10px !important;
    border-radius: 6px;
    color: var(--anzhiyu-white);
    backdrop-filter: blur(5px);
}

.recent-post-info-top-tips.right {
    right: 0;
}

.recent-post-info-top-tips.left {
    left: 0;
}

[data-theme='dark'] .recent-post-info-top-tips {
    filter: brightness(0.8);
}

.article-meta-wrap {
    position: inherit !important;
    padding: 0 !important;
}

.article-meta.sticky-warp,
span.newPost {
    color: #ff7242;
}

.article-categories-original,
span.newPost {
    display: flex;
    gap: 5px;
    align-items: center;
}

span.article-meta.tags,
span.post-meta-date {
    height: 26px;
    line-height: 26px;
}

.recent-post-info .recent-post-info-top {
    padding: 0 !important;
}

.unvisited-post {
    display: none !important;
}

@media screen and (max-width: 768px) {
    #recent-posts > .recent-post-item > .recent-post-info > .article-meta-wrap {
        bottom: 0 !important;
    }
    .recent-post-info-top-tips.right {
        right: inherit;
    }
}